.lotPara {
    opacity: .7;
    width: 55%;
    font-size: 20px;
    font-weight: 200;
    margin: auto;
    margin-top: 30px;
}
.LotteryMainContent {
    color: white; 
    width: 100%; 
    text-align: center; 
    margin-top: 10%;
}
.timeRem {
    font-size: 28px !important; 
    font-weight: 400; 
}
@media only screen and (max-width: 425px) {
    .lotPara {
        width: 70%;
    }
    .LotteryMainContent {
        margin-top: 12% !important;
    }
    .timeRem {
        font-size: 22px !important; 
        font-weight: 400;
        margin: 0px !important;
    }
}