

html,
body,
#root {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

*{
  margin:0;
  padding: 0;
}
body {
  font-family: system-ui;
  margin: 0px;
  // padding: 0px;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}
.main {
  background-size: cover;
  width: 100vw; 
  height: 100%; 
  object-fit:contain;
  
}

.landingPageDialog {
  cursor: pointer;
  text-decoration: none;
  color: white;
  display: inline-grid;
}

.landingPageDialog:hover {
  color: rgb(202, 100, 6);
}

.mintingPageBtnHeader {
  gap: 6px;
  font-size: 18px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  border-radius: 20px;
  padding-left: 24px;
  padding-right: 24px;
  border: 0px;
}
.slick-prev {
  left: -5% !important;
  z-index: 1;
}
.slick-next {
  right: -5% !important;
  z-index: 1;
}
.slick-initialized .slick-slide {
  display: flex !important;
  justify-content: center !important;
  width: 220px !important;
}
.slick-slider{
    margin: auto;
    width: 870px;
}
.faqs {
  height:calc(100vh - 18rem);
  overflow-y: scroll;
}
.faqs::-webkit-scrollbar {
  display: none;
}
a {
  text-decoration: none;
}

.backClr {
  overflow:hidden;
  background: black;
  height: 100vh;
  width: 100vw;
}

