button {
    border: 0px;
    margin-top: 60px;
    height: 50px;
    font-size: 20px;
    font-weight: 300;
    color: white;
    cursor: pointer;
    background-color: #CA6406;
    border-radius: 25px;
    padding-left: 40px; 
    padding-right: 40px;
}
.paragraph {
    opacity: .7;
    width: 55%;
    font-size: 20px;
    font-weight: 200;
    margin: auto;
    margin-top: 20px;
}
h1 {
    font-size: 50px; 
    font-weight: 500; 
    // margin: 10px;
}
.LetsMint {
    font-size: 28px;
}

@media only screen and (max-width: 425px) {
    h1 {
        font-size: 34px !important;
    }
    .LetsMint {
        font-size: 20px !important; 
        margin-top: 20% !important;
    }
    .paragraph {
        font-size: 16px !important;
        width: 85% !important;
        margin-top: 20px !important;
    }
    button {
        margin-top: 26px !important;
        height: 40px !important;
        border-radius: 20px !important;
        padding-left: 40px !important; 
        padding-right: 40px !important;
        font-size: 18px !important;
    }
}
@media only screen and (max-width: 1024px) {
    button {
        margin-top: 40px;
        height: 44px;
        background-color: #CA6406;
        border-radius: 22px;
        padding-left: 36px; 
        padding-right: 36px;
    }
    .paragraph {
        font-size: 18px;
    }
    h1 {
        font-size: 40px; 
    }
    .LetsMint {
        font-size: 24px;
        margin-top: 10%;
    }
}