.LandingPageMain {
  color: white;
  width: 100%;
  text-align: center;
  .welcomeToText {
    font-size: 28px;
    margin-top: 12%;
  }
  h1 {
    font-size: 50px;
    font-weight: 500;
    // margin: 10px;
  }
  .paragraph {
    opacity: 0.7;
    width: 55%;
    font-size: 20px;
    font-weight: 200;
    margin: auto;
    margin-top: 20px;
  }
  button {
    border: 0px;
    margin-top: 60px;
    height: 50px;
    font-size: 20px;
    font-weight: 300;
    color: white;
    cursor: pointer;
    background-color: #ca6406;
    border-radius: 25px;
    padding-left: 60px;
    padding-right: 60px;
  }
}
.storyMain {
  margin: auto;
  margin-top: 4%;
  display: grid;
  width: fit-content;
  grid-template-columns: 150px 150px 150px 150px;
  justify-content: space-around;
  gap: 50px;
  z-index: 22;
  .storyCard {
    height: 190px;
    width: 150px !important;
    background-color: #9b6f4c;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    .cardImg {
      height: 120px;
      width: 120px;
      border-radius: 60px;
      border: 1px solid black;
    }
    .cardText {
      margin-top: 14px;
      font-size: 18px;
      font-weight: 500;
      color: white;
      text-align: center;
    }
  }
}
.storyDiv {
  font-size: 20px;
  font-weight: 100;
  color: white;
  width: 80%;
  opacity: 0.7;
}
.faqHeading {
  font-size: 26px !important;
  color: white;
}
.faqQustions {
  font-size: 24px !important;
  color: #ffdcbb;
}
.teamProfileDiv {
  width: 200px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}
.footerPara {
  width: 70%;
  font-size: 16px;
}
.footer {
  display: flex;
  color: #d2d2d2;
  font-weight: 200;
  padding: 20px;
}
.footerDiv {
  width: 33.33%;
}
.footerSection {
  display: inline-block;
}
.heading {
  font-size: 34px;
  font-weight: 400;
}

.hr {
  opacity: 0.7;
  width: 65%;
}
.drawerContainer {
  display: block;
}
.accordion {
  width: 70% !important;
  margin: auto !important;
  margin-top: 7% !important;
}

.storyDialog {
  display: flex;
  margin: 4%;
  justify-content: space-between;
}
.storyImg {
  object-fit: contain;
  border-radius: 20px;
  width: 36%;
}
.rollingImg {
  position: absolute;
  height: 10%;
  width: 15%;
  margin-left: 80%;
  animation: myfirst 1.5s linear 2s infinite alternate;
  -webkit-transition: all 1s ease-out;
  -moz-transition: all 1s ease-out;
  -ms-transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  transition: all 1s ease-out;
}

@keyframes myfirst {
  0% {
    margin-top: 0%;
    transform: rotateY(0deg);
  }
  100% {
    margin-top: 23%;
    transform: rotateY(180deg);
  }
}
@media only screen and (max-width: 1024px) {
  @keyframes myfirst {
    0% {
      margin-top: 0%;
      transform: rotateY(0deg);
    }
    100% {
      margin-top: 33%;
      transform: rotateY(180deg);
    }
  }
  .LandingPageMain {
    h1 {
      font-size: 40px;
    }
    .welcomeToText {
      font-size: 24px;
      margin-top: 12%;
    }
    .paragraph {
      font-size: 18px;
    }
    button {
      margin-top: 40px;
      height: 44px;
      background-color: #ca6406;
      border-radius: 22px;
      padding-left: 50px;
      padding-right: 50px;
    }
  }
  .faqHeading {
    font-size: 20px !important;
  }
  .rollingImg {
    height: 140px;
    width: 140px;
    margin-left: 80%;
  }
}

@media only screen and (min-width: 769px) {
  .drawerContainer {
    display: none;
  }
  .buttonContainer {
    display: none;
  }
  .myDrawer {
    display: none;
  }
}

@media only screen and (max-width: 320px) {
  .rollingImg {
    width: 15%;
    height: 10%;
  }
  @keyframes myfirst {
    0% {
      margin-top: 0%;
      transform: rotateY(0deg);
    }
    100% {
      margin-top: 53%;
      transform: rotateY(180deg);
    }
  }
}

@media only screen and (max-width: 768px) {
  @keyframes myfirst {
    0% {
      margin-top: 0%;
      transform: rotateY(0deg);
    }
    100% {
      margin-top: 53%;
      transform: rotateY(180deg);
    }
  }
  .storyMain {
    row-gap: 35px;
    column-gap: 35px;
    grid-template-columns: 25% 25%;
    justify-content: space-around;
    .storyCard {
      height: 160px;
      width: 130px !important;
      border-radius: 12px;
      margin-left: -40%;
      .cardImg {
        height: 100px;
        width: 100px;
        border-radius: 50px;
      }
      .cardText {
        margin-top: 6px;
        font-size: 16px;
      }
    }
  }
  .storyDiv {
    font-size: 18px;
  }
  .faqQustions {
    font-size: 20px;
  }
  .storyDialog {
    flex-direction: column-reverse;
  }
  .storyImg {
    width: 50%;
  }
  .rollingImg {
    margin-left: 74%;
  }
}

@media only screen and (max-width: 768px) {
  .emptyDrawer {
    display: none;
  }
  .buttonEmpty {
    display: none;
  }
  .accordion {
    margin-top: 6% !important;
  }
}

@media only screen and (max-width: 600px) {
  .footer {
    flex-direction: column;
    div {
      width: 100%;
    }
  }
  .footerDiv {
    width: auto;
  }
  .footerPara {
    text-align: center;
    width: 100%;
  }
  .footerSection {
    width: auto !important;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
  }
}

@media only screen and (max-width: 425px) {
  @keyframes myfirst {
    0% {
      margin-top: 0%;
      transform: rotateY(0deg);
    }
    100% {
      margin-top: 100%;
      transform: rotateY(180deg);
    }
  }
  .LandingPageMain {
    h1 {
      font-size: 30px;
    }
    .welcomeToText {
      font-size: 20px;
      margin-top: 20%;
    }
    .paragraph {
      font-size: 16px;
      width: 85%;
      margin-top: 20px;
    }
    button {
      margin-top: 26px;
      height: 40px;
      background-color: #ca6406;
      border-radius: 20px;
      padding-left: 40px;
      padding-right: 40px;
      font-size: 18px;
    }
  }
  .storyMain {
    .storyCard {
      height: 130px;
      width: 100px !important;
      border-radius: 10px;
      .cardImg {
        height: 90px;
        width: 90px;
        border-radius: 45px;
        margin-top: 10px !important;
      }
      .cardText {
        margin-top: 4px;
        font-size: 14px;
      }
    }
  }
  .faqQustions {
    font-size: 18px !important;
  }
  .heading {
    font-size: 24px;
    width: inherit;
  }
  .hr {
    width: 50%;
  }
  .accordion {
    margin-top: 8% !important;
  }
  .storyImg {
    width: 100% !important;
  }
  .rollingImg {
    margin-left: 63%;
    height: 6%;
    width: 30%;
  }
}
